@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


a{
    text-decoration: none;
}
.wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-row-gap: 2rem;
  row-gap: 2rem;
  padding: 2rem 8rem;
  background: rgb(230, 255, 229);
}

@media (max-width:786px) {
  .wrapper {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }
}

.card {
    width: 80%;
    height: 26rem;
    border-radius: .8rem;
    box-shadow: 0 5px 10px rgba(28, 42, 241, 0.3);
    font-family: 'Nunito', sans-serif;
    background: #fff;
    transition: all .1s ease-out;
    /* border: 2px solid rgb(84, 22, 255,0.7); */
}
.card:hover {
    /* border: 2px solid rgb(84, 22, 255,0.7); */
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
}
.avatar {
    border-top-left-radius: .8rem;
    border-top-right-radius: .8rem;
    width: 100%;
    height: 16rem;
    object-fit: cover;
}
.card-desc {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
}
.card-desc h2 {
    position: relative;
    font-weight: 800;
    font-size: 1.5rem;
    color: black;
    margin: .5rem 0 0 0;
}
.card-desc p {
    font-size: 1rem;
    font-weight: 700;
    margin: .7rem;
    color: #474747;
}
.contactbtn {
    position: relative;
    background-image: linear-gradient(to right, #4776E6 0%, #8E54E9  51%, #4776E6  100%);
    background-size: 200% auto;
    width: 80%;
    padding: .8rem;
    text-align: center;
    text-transform: uppercase;
    transition: 0.5s;
    color: #fff;
    font-size: 1rem;
    font-weight: 600;
    border-radius: .8rem;
}
.contactbtn:hover {
    background-position: right center;
    color: #fff;
}
@media only screen and (max-width: 414px) {
    .wrapper {
        padding: 2rem;
      }
    .card {
        width: 100%;
    }    
}
.home{
    display: flex;
    flex-wrap: wrap;
    grid-gap: .5rem;
    gap: .5rem;
    min-height: 100vh;
    align-items: center;
    scroll-behavior: smooth;
    background: rgb(230, 255, 229);
    font-family: 'Nunito', sans-serif;
}
.home .content{
    flex: 1 1 40rem;
    margin-top: -10rem;
    padding: 1.5rem;
}
.home .image{
    flex: 1 1 40rem;
    margin-top: -6rem;
}
.home .image img{
    width: 90%;
    padding: 1rem;
    -webkit-animation: float 4s linear infinite;
            animation: float 4s linear infinite;
}
@-webkit-keyframes float{
    0%, 100%{
        -webkit-transform: translateY(0rem);
                transform: translateY(0rem);
    }
    50%{
        -webkit-transform: translateY(3rem);
                transform: translateY(3rem);
    }
}
@keyframes float{
    0%, 100%{
        -webkit-transform: translateY(0rem);
                transform: translateY(0rem);
    }
    50%{
        -webkit-transform: translateY(3rem);
                transform: translateY(3rem);
    }
}
.home .content h3{
    font-size: 3.5rem;
    font-weight: 800;
    color: #012970;
}
.home .content p{
    font-size: 2rem;
    font-weight: 800;
    color: #012970;
    margin: 0;
}

@media only screen and (max-width: 414px) {
    .home .content{
        margin-top: -2rem;
    }
    .home .image{
        margin-top: -8rem;
    }
    .home .content h3{
        font-size: 3rem;
    }
    .home .content p{
        font-size: 1.5rem;
    }
}
.loader {
  position: absolute;
  top: 50%;left: 50%;
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  border: 10px solid rgba(110, 5, 209, 0.13);
  border-top: 10px solid rgb(128, 6, 241);
  -webkit-animation: rotate 1.5s infinite linear;
          animation: rotate 1.5s infinite linear;
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: translate(-50%,-50%) rotate(0deg);
            transform: translate(-50%,-50%) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(-50%,-50%) rotate(360deg);
            transform: translate(-50%,-50%) rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: translate(-50%,-50%) rotate(0deg);
            transform: translate(-50%,-50%) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(-50%,-50%) rotate(360deg);
            transform: translate(-50%,-50%) rotate(360deg);
  }
}
header{
    display: flex;
    align-items: center;
    font-family: 'Nunito', sans-serif;
    justify-content: space-between;
    background: rgb(101, 7, 224);
    padding: 1rem 10%;
    box-shadow: 0 1rem 1rem rgba(65, 43, 43, 0.1);
}
header .logo{
    font-size: 2rem;
    font-weight: bolder;
    color: #fff;
}
button{
    background: #fff;
    border-radius: .3rem;
    padding: .6rem 1.5rem;
    font-family: 'Nunito', sans-serif;
    font-size: 1.2rem;
    font-weight: 700;
    color: rgb(101, 7, 224);
    background: #fff;
    border: none;
    outline: none;
    cursor: pointer;
    transition: .3s ease;
}
button:hover{
    color: rgb(101, 7, 224);
    background: rgb(255, 230, 6);
}
@media only screen and (max-width: 414px) {
    header .logo{
        font-size: 1.2rem;
    }
    button{
        padding: .4rem 1rem;
        font-size: 1rem;
    }
}
